

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app__main-block {
  position:  absolute;
  top: 0;
  height: 90vh;
} 

.lost {
  height: 53vh;
  display: flex;
  align-items: center;
  justify-content: center;
}


.beer__main-list {
  position: relative;
} 



.pop-up {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  background: green;
  height: 70vh;
  width: 100vw;

}


.pop-up.hidden {
  display: none;
}